.carousel{

    width:60%;
    height:700px;
    background-color: black;
}

.carouselInner{
    height: 100%;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
}


.carouselInner .left{
    flex:5%;
    height:100%;
    background-color: rgb(0,0,0, 0.6);
    display: grid;
    place-items: center;
    color: white;
    cursor: pointer;
}

.carouselInner .center{
    flex:80%;
    height:100%;
   display: grid;
   place-items: center;
   font-family: Arial, Helvetica, sans-serif;
   text-align-last:center;
   text-align: justify;
}

.carouselInner .right{
    flex:5%;
    height:100%;
    background-color: rgb(0,0,0, 0.6);
    display: grid;
    place-items: center;
    color: white;
    cursor: pointer;
}


h1{
  font-size:5rem;

}
p{
    font-size:2rem;
}


h1,p{
    background-color: rgb(255, 255,255,0.8);
    padding: 20px;
    border-radius:9px;
}